@import url('../googlefonts/fontgoogle.css');
body {
    background-color: black;
    color: whitesmoke;
    font-family: 'Roboto Mono', monospace;
    font-size: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 97.6vh;
    overflow: hidden;
}
legend {
    font-size: 17px;
    font-weight: bold;
}
fieldset {
    border: 1px solid white;
    box-sizing: border-box;
    margin-top: 5px;
    margin: 0;
}
h1 {
    font-size: 28px;
    margin: 0;
    text-align: center;
}
.tagline {
    text-align: center;
}
a {
    color: salmon;
    text-decoration: none;
    border-bottom: 1px dashed rgb(255, 38, 0);
}
i {
    width: 19px;
    text-align: center;
}
.container {
    flex-direction: column;
}
.profile {
    width: 100%;
}
.portofolio {
    padding-top: 15px;
}
/* p {
    line-height: 1px;
} */
ul {
    list-style: none;
    padding: 0;
    margin: 0;
}